
















































export default {
	data() {
		return {
			title: 'openLocation'
		}
	},
	methods: {
		openLocation: function (e) {
			console.log(e)
			var value = e.target.value
			uni.openLocation({
				longitude: Number(value.longitude),
				latitude: Number(value.latitude),
				name: value.name,
				address: value.address
			})
		}
	}
}



